/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-size: 1.6rem;
  line-height: 1.8;
  background: #002222;
  margin: 0;
}

@font-face {
  font-family: "Pacifico";
  font-style: normal;
  font-weight: 400;
  src: local("Pacifico Regular"), local("Pacifico-Regular"), url(https://fonts.gstatic.com/s/pacifico/v12/FwZY7-Qmy14u9lezJ-6H6MmBp0u-.woff2) format("woff2");
  font-display: swap;
}
section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  min-height: 100vh;
  overflow: hidden;
}
@media (min-width: 600px) {
  section {
    min-height: 100vh;
    justify-content: center;
  }
}

#about .container,
#skills .container,
#work .container {
  margin: 5rem 0;
  height: 100%;
}
@media (min-width: 600px) {
  #about .container,
#skills .container,
#work .container {
    margin: 0 auto;
  }
}

.container {
  position: relative;
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 600px) {
  .container {
    row-gap: 1em;
  }
}
@media (min-width: 1007px) {
  .container {
    flex-direction: row;
    align-items: center;
  }
}

a {
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
}

p {
  font-family: "Lato", sans-serif;
  font-size: 1.5rem;
  margin: 0;
}

label {
  font-family: "Lato", sans-serif;
  color: #FFF0D3;
}

h1,
h2,
h3,
h4,
h5 {
  font-family: "Lato", sans-serif;
  margin: 10em;
  color: black;
  margin: 0;
  font-weight: 500;
  text-transform: uppercase;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 2.8rem;
}

h3 {
  font-size: 2.4rem;
}

h4 {
  font-size: 1.8rem;
  margin: 0;
}

h5 {
  color: #005555;
  font-size: 1.4rem;
}

img {
  width: 100%;
  height: auto;
}

#about,
#work {
  background-color: #FFF0D3;
}

.personal-links {
  display: flex;
  z-index: 1;
}
.personal-links img {
  width: 9vw;
  max-width: 4rem;
}
@media (min-width: 1007px) {
  .personal-links {
    position: absolute;
  }
  .personal-links img {
    width: 3vw;
    min-width: 5rem;
  }
}

.bg-img {
  max-width: 100%;
  height: 100vh;
}

.item-unvisible {
  visibility: hidden;
}

.item-visible {
  visibility: visible;
}

.text {
  text-align: justify;
  line-height: 1.8;
  padding: 2rem;
}
@media (min-width: 600px) {
  .text p {
    font-size: 1.8rem;
  }
}
@media (min-width: 1007px) {
  .text {
    width: 100%;
    padding: 0 3rem;
    text-align: justify;
  }
  .text .collapse {
    display: none;
  }
}